import React from 'react'
// CSS and JS
import './AppStyle.css';
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min.js'



import axios from 'axios';
import { useState } from 'react';

function App() {

  const [reportVisibility, setReportVisibility] = useState("none")
  const [report, setReport] = useState({
    city: "",
    feels_like: "",
    grnd_level: "",
    humidity: "",
    pressure: "",
    sea_level: "",
    temp: "",
    temp_max: "",
    temp_min: "",
    clouds: "",
    description: ""
  })

  const [cityName, setCityName] = useState("")

  const onChangeFun = (e) => {
    setReportVisibility("none")
    const { value } = e.target;
    setCityName(value)
  }

  const getWeather = (e) => {
    e.preventDefault();
    axios.post('/weather', {
      cityName
    }).then((res) => {
      if (res.data.reportData.message === "city not found") {
        window.alert(res.data.reportData.message + " so please enter correct name")
      } else {
        setReportVisibility("block")
        var { main, weather } = res.data.reportData;
        const { feels_like, grnd_level, humidity, pressure, sea_level, temp, temp_max, temp_min } = main;
        const clouds = weather[0].main;
        var { description } = weather[0];
        setReport({
          city: res.data.reportData.name,
          feels_like,
          grnd_level,
          humidity,
          pressure,
          sea_level,
          temp,
          temp_max,
          temp_min,
          clouds,
          description
        })
        setCityName("")
      }
    }).catch((err) => {
      if (err.code === "ERR_BAD_REQUEST") {
        window.alert("Enter city name")
        setReportVisibility("none")
      } else {
        setReportVisibility("block")
      }
    })

  }

  return (
    <>
      <div className='search-container'>
        <form action="/" method="post">
          <input type="text" placeholder='Enter city' name="cityName" value={cityName} onChange={onChangeFun} />
          <button onClick={getWeather} className='btn btn-primary sendBtn'>Send</button>
        </form>
      </div>

      <div className='weatherReport' style={{ display: reportVisibility }}>
        <div className="row">
          <div className='cityName'>
            <div className="col-12">
              <h2>City name: {report.city}</h2>
            </div>
          </div>
          <div className="col-6">Feels_like : {report.feels_like} ℃</div>
          <div className="col-6">Humidity : {report.humidity} %</div>
          <div className="col-6">Pressure : {report.pressure} mm</div>
          <div className="col-6">Temp : {report.temp} ℃</div>
          <div className="col-6">Clouds :{report.clouds}</div>
          <div className="col-6">Description : {report.description}</div>
        </div>
      </div>
    </>
  );
}

export default App;
